/*
==============================================================================================================
------------------------------------------ Шрифт «Roboto Condensed» ------------------------------------------
==============================================================================================================
*/
@font-face {
  font-family: 'Roboto Condensed';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Roboto Condensed Regular"), local("RobotoCondensed-Regular"), url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff");
  unicode-range: U+0400-045F, U+0490-0491, U+04B0-04B1, U+2116; }

@font-face {
  font-family: 'Roboto Condensed';
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("Roboto Condensed Bold"), local("RobotoCondensed-Bold"), url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff");
  unicode-range: U+0400-045F, U+0490-0491, U+04B0-04B1, U+2116; }

/*
==============================================================================================================
----------------------------------------------- Шрифт «PT Mono» ----------------------------------------------
==============================================================================================================
*/
@font-face {
  font-family: 'PT Mono';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("PT Mono Regular"), local("PTMono-Regular"), url("../fonts/PTMono-Regular.woff2") format("woff2"), url("../fonts/PTMono-Regular.woff") format("woff"); }

*,
*::before,
*::after {
  box-sizing: inherit; }

html {
  box-sizing: border-box; }

body {
  display: flex;
  flex-direction: column;
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  background-color: #1B2931;
  background-image: url("../img/background-header.png");
  background-repeat: no-repeat;
  background-position: top center;
  overflow-x: hidden; }

footer {
  margin-top: auto; }

.visually-hidden {
  width: 1px;
  height: 1px;
  position: absolute;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

/* *****************************
*** 2. Вспомогательные стили ***
***************************** */
.container {
  width: 100%;
  max-width: 1424px;
  margin: 0 auto;
  padding: 0; }

.button {
  display: inline-block;
  width: auto;
  min-width: 180px;
  min-height: 45px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 15px;
  font-weight: 700;
  line-height: 16px;
  color: #FFFFFF;
  text-transform: uppercase;
  background-color: transparent;
  border: 1px solid #FFFFFF;
  border-radius: 5px; }

/* **********************
*** 3. Основные стили ***
********************** */
.site-header {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin-top: 33px; }

.site-header__logo {
  margin-left: 13px; }

.site-header__contacts-link {
  position: relative;
  margin-right: 113px;
  color: #FFFFFF;
  text-decoration: none; }
  .site-header__contacts-link:last-of-type {
    margin-right: 0; }

.site-header__contacts-link-icon {
  position: absolute;
  top: 50%;
  transform: translateY(-50%); }
  .site-header__contacts-link-icon--mail {
    left: -38px; }
  .site-header__contacts-link-icon--phone {
    left: -34px; }

.site-header__callback-button {
  margin-left: 39px;
  cursor: pointer; }
